import * as React from "react";
let ReactDOM = require("react-dom");
import './Style.css';

import {FormBuilderOptions} from "FormBuilder/Core/FormBuilder.options";
import {CurrencyManager} from "FormBuilder/Utilities/Managers/CurrencyManager";
import {ProductFieldsDesigner} from "ProductFieldBuilder/Designer/ProductFieldsDesigner";
import {FormBuilderModel} from "FormBuilder/Core/FormBuilder.Model";
import {ProductData} from "FormBuilder/Core/FormBuilder";
import {FormBuilderDesignerModel} from "ProductFieldBuilder/Designer/FormBuilderDesigner/Core/FormBuilderDesigner.Model";

class ProductFieldBuilderApp extends React.Component<ProductFieldBuilderProps, ProductFieldBuilderState> {
    static defaultProps: Partial<ProductFieldBuilderProps> = {};
    Designer:ProductFieldsDesigner;
    constructor(props: ProductFieldBuilderProps) {
        super(props);
        this.state = {};
    }

    public render() {

        return (
            <div className={'rednaoproductdesigner'}>
                <ProductFieldsDesigner Model={this.props.Model}/>
            </div>
        )
    }
}

CurrencyManager.Initialize(rednaoProductDesigner.WCCurrency);

//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface ProductFieldBuilderProps {
    Model:FormBuilderModel;
}

interface ProductFieldBuilderState {

}
let options=rednaoProductDesigner.Options;
try{
    options=JSON.parse(options)
}catch(Exception)
{
    options=null;
}

let formOptions=new FormBuilderOptions().Merge(options);
let model=new FormBuilderDesignerModel(formOptions,null,new ProductData()).Initialize();
let builder:ProductFieldBuilderApp;

ReactDOM.render(<ProductFieldBuilderApp Model={model} ref={x=>builder=x}/>,document.getElementById('rednao-advanced-products-designer'));

let post=document.getElementById('post');
if(post!=null)
    post.addEventListener('submit',()=>{
        RefreshObject();

    });
declare let wp:any;

if(wp!=null&&wp.data!=null&&wp.data.subscribe!=null)
{
    wp.data.subscribe(function () {
        var isSavingPost = wp.data.select('core/editor')?.isSavingPost();
        var isAutosavingPost = wp.data.select('core/editor')?.isAutosavingPost();

        if (isSavingPost || !isAutosavingPost) {
            RefreshObject();

        }
    })
}


function RefreshObject(){
    let optionsToSave:any=model.GetOptions();
    if(optionsToSave.Rows.length==0)
        optionsToSave ='' as any;
    (document.getElementById('rednao_advanced_product_options_input') as HTMLInputElement).value=JSON.stringify(optionsToSave);
    return true;
}


declare let require;
let context=require.context("./", true, /.tsx?$/);
let library:any={};
for(let key of context.keys())
{
    library[key.substring(0,key.lastIndexOf('.'))]=key;
}



export default function (libraryName) {

    libraryName='./'+libraryName.substring(12);//removed /InternalShared and adding ./

    if(library[libraryName]==null)
        throw 'Library not found '+libraryName;

    return context(library[libraryName]);


}

//#endregion "My Region"