
let ReactDOM = require("react-dom");
import * as React from "react";
import {FormBuilderOptions} from "FormBuilder/Core/FormBuilder.options";
import {FormBuilder, ProductData} from "FormBuilder/Core/FormBuilder";
import {CurrencyManager} from "FormBuilder/Utilities/Managers/CurrencyManager";
import {FormBuilderModel} from "FormBuilder/Core/FormBuilder.Model";




declare let ProductBuilderOptions:{
    Options:FormBuilderOptions,
    WCCurrency:any,
    Product:ProductData;
    PreviousData:any[];
    Variations:{
        Attributes:any;
    }
};

function MaybeLoadForm(){
    let element=document.getElementById('RNAddToCartContainer');
    if(element==null&&ProductBuilderOptions!=null) {
        setTimeout(MaybeLoadForm, 100);
        return;
    }

    CurrencyManager.Initialize(ProductBuilderOptions.WCCurrency);
    let options=new FormBuilderOptions().Merge(ProductBuilderOptions.Options);
    let model=new FormBuilderModel(options,null,ProductBuilderOptions.Product,ProductBuilderOptions.PreviousData).Initialize();
    model.ExecuteFirstCalculation();
    ReactDOM.render(<FormBuilder  Model={model} PreviousData={ProductBuilderOptions.PreviousData}/>,document.getElementById('RNAddToCartContainer'));
}

MaybeLoadForm();
